<template>
	<div id="activity-delivery-fee">
		<public-header :title="page_title"></public-header>
		<div class="content">
			<van-cell-group class="margin-10-t">
				<van-cell>
					<div slot="title" class="">开始时间</div>
					<div slot="right-icon" class="c-gray" @click="onToggleDatePicker('start')">
						{{this.datePicker.start ? this.datePicker.start: '点击选择'}}
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
				<van-cell>
					<div slot="title" class="">结束时间</div>
					<div slot="right-icon" class="c-gray" @click="onToggleDatePicker('end')">
						{{this.datePicker.end ? this.datePicker.end: '点击选择'}}
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
			</van-cell-group>
			<div class="activity-box margin-10-t">
				<template v-if="filter.items.type == 'newMember'">
					<div class="activity-col">
						<span>门店新客立减</span>
						<div class="input-box">
							 <input v-model="options[0].back" />
						</div>
						<span>元</span>
					</div>
				</template>
				<template v-else>
					<div class="activity-col" v-for="(item, index) in 4">
						<span>满</span>
						<div class="input-box">
							<input v-model="options[index].condition" />
						</div>
						<span>{{discount_title}}</span>
						<div class="input-box">
							<input v-model="options[index].back" />
						</div>
						<span>{{discount_cn}}</span>
					</div>
				</template>
			</div>
			<div class="padding-15">
				<van-button size="normal" :disabled="!islegal" block class="font-16 bg-info" @click="onSubmit">确认并保存</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
		<!--时间弹出层-->
		<van-popup class="popup-time" v-model="datePicker.status" position="bottom">
			<van-datetime-picker
				v-model="datePicker.currentDate"
				type="datetime"
				:min-data="datePicker.currentDate"
				:max-date="datePicker.maxDate"
				@confirm="onConfirmTime"
				@cancel="onToggleDatePicker"
			/>
		</van-popup>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				showPreLoading: true,
				islegal: false,
				page_title: '',
				discount_title: '',
				discount_cn: '',
				filter: {
					items: {
						type: ''
					}
				},
				datePicker: {
					type: '',
					maxDate: new Date(2050, 12),
					currentDate: new Date(),
					status: false,
					start: '',
					end: '',
				},
				options:[
					{
						condition: 0,
						back: 0,
					},
					{
						condition: 0,
						back: 0,
					},
					{
						condition: 0,
						back: 0,
					},
					{
						condition: 0,
						back: 0,
					}
				]
			}
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'manage/activity/index/activity_other',
					data: {
						filter: this.filter.items
					},
					success: function(res) {
						that.islegal = true;
						that.page_title = res.page_title;
						that.discount_title = res.discount_title;
						that.discount_cn = res.discount_cn;
						let nowtime = that.util.formatDate(that.datePicker.currentDate);
						that.datePicker.start = that.datePicker.end = nowtime;
					},
					fail: function(res) {
						var url = '';
						if(res.errno == -1000) {
							url = that.util.getUrl({path: 'pages/activity/index'});
						}
						that.util.$toast(res.message, url, 1000);
					}
				})
			},
			onToggleDatePicker(type) {
				this.datePicker.status = !this.datePicker.status;
				if(type) {
					this.datePicker.type = type;
				}
			},
			onConfirmTime(value) {
				let time = this.util.formatDate(value);
				this.datePicker[this.datePicker.type] = time;
				this.onToggleDatePicker();
			},
			onSubmit() {
				var that = this;
				if(!that.filter.items.type) {
					that.util.$toast('请先选择活动类型');
					return false;
				}
				setData({
					vue: that,
					url: 'manage/activity/index/activity_other',
					data: {
						type: that.filter.items.type,
						params: JSON.stringify({
							starttime: that.datePicker.start,
							endtime: that.datePicker.end,
							options: that.options
						})
					},
					redirect: that.util.getUrl({path: 'pages/activity/index'})
				});
			}
		},
		mounted() {
			this.onLoad();
		},
		created() {
			this.query = this.$route.query;
			if(this.query && this.query.type) {
				this.filter.items.type = this.query.type;
			}
		}
	}
</script>

<style>
#activity-delivery-fee .content .activity-box .activity-col{
	display: flex;
	padding: 8px 16px;
	position: relative;
	background-color: #FFF;
}
#activity-delivery-fee .content .activity-box .activity-col span{
	font-size: 16px;
	float: left;
	line-height: 32px;
}
#activity-delivery-fee .content .activity-box .activity-col .input-box{
	display: inline-block;
	width: 66px;
	height: 30px;
	border: 1px solid #E6E6E6;
	margin: 0 12px;
	line-height: 14px;
	float: left;
}
#activity-delivery-fee .content .activity-box .activity-col .input-box input{
	display: inline-block;
    width: 100%;
    height: 100%;
    border: 0;
    outline: 0;
    text-align: center;
}
#activity-delivery-fee .content .activity-box .activity-col .icon-delete{
	position: absolute;
	right: 0;
	top: 50%;
	transform: translate(0, -50%);
	margin-right: 16px;
	color: #5F5F5F;
	font-size: 16px;
}
#activity-delivery-fee .content .activity-box .activity-add{
	padding: 8px 16px;
	text-align: center;
	color: #FF2B4D;
	cursor: pointer;
	font-size: 16px;
}
#activity-delivery-fee .content .activity-box .activity-col:nth-child(even){
	background-color: #F9F9F9;
}
#activity-delivery-fee .van-popup.popup-time .van-picker__cancel{
	color: #7a7a7a;
}
#activity-delivery-fee .van-popup.popup-time .van-picker__confirm{
	color: #33aafc;
}
</style>


